@import url(https://allfont.ru/allfont.css?fonts=exo-20-thin);
/* ----------------fonts-------------------- */
@font-face {
  font-family: "fontea3fdfa648b7ec0e4183621443f549e3";

  src: url("https://nomail.com.ua/files/eot/ea3fdfa648b7ec0e4183621443f549e3.eot?#iefix")
      format("embedded-opentype"),
    url("https://nomail.com.ua/files/woff/ea3fdfa648b7ec0e4183621443f549e3.woff")
      format("woff"),
    url("https://nomail.com.ua/files/woff2/ea3fdfa648b7ec0e4183621443f549e3.woff2")
      format("woff2");
}

@font-face {
  font-family: "TrebuchetMS";
  src: local("TrebuchetMS"),
    url("https://nomail.com.ua/files/woff/ea3fdfa648b7ec0e4183621443f549e3.woff")
      format("woff");
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


.card-container {
  margin: 0 auto;
  max-width: 1120px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  box-sizing: border-box;
}
.card-wrapper {
  margin: 0 40px 20px;
  max-width: 320px;
  width: 100%;
}
.card-wrapper:first-child {
  margin-left: 0;
}
.card-wrapper:last-child {
  margin-right: 0;
}
.card-border {
  position: relative;
  width: 100%;
  min-height: 480px;
  border-radius: 14px;
  -webkit-clip-path: polygon(15% 0, 100% 0, 100% 100%, 0 100%, 0 10%);
          clip-path: polygon(15% 0, 100% 0, 100% 100%, 0 100%, 0 10%);
  background-color: #1698d9;
  margin-bottom: 12px;
}

.card {
  position: absolute;
  top: 4px;
  left: 4px;
  width: calc(100% - 8px);
  height: calc(100% - 8px);
  background-color: #f2f2f2;
  border-radius: 11px;
  -webkit-clip-path: polygon(14.7% 0, 100% 0, 100% 100%, 0 100%, 0 9.7%);
          clip-path: polygon(14.7% 0, 100% 0, 100% 100%, 0 100%, 0 9.7%);
  cursor: pointer;
}
.card--disabled::after {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: #fff;
  opacity: 0.5;
  z-index: 10;
  left: 0;
  top: 0;
  cursor: not-allowed;
  border-radius: 11px;
}

.card-content {
  padding: 22px 50px;
  position: relative;
  height: 100%;
  box-sizing: border-box;
}
.card-name {
  color: #666666;
  font-family: TrebuchetMS, sans-serif;
  font-size: 16px;
  font-weight: 400;
}
.card-title {
  color: #000000;
  font-family: TrebuchetMS, sans-serif;
  font-size: 48px;
  font-weight: 700;
  margin: 0;
  display: block;
}
.card-taste {
  font-family: TrebuchetMS, sans-serif;
  font-size: 14px;
  font-weight: 400;
  line-height: 16px;
  display: block;
  margin-bottom: 16px;
}
.card-portions {
  color: #666666;
  font-family: TrebuchetMS, sans-serif;
  font-size: 14px;
  font-weight: 400;
  line-height: 16px;
}
.card-taste {
  color: #000000;
  font-family: TrebuchetMS, sans-serif;
  font-size: 24px;
  font-weight: 700;
}

.card-name,
.card-gift {
  color: #666666;
  font-family: TrebuchetMS, sans-serif;
  font-size: 16px;
  font-weight: 400;
  text-decoration: none;
}
.card-name--pink {
  color: #d91667;
}

.card-img {
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 273px;
  background: url(/cat-food/static/media/cat.4766b0a2.png) no-repeat bottom left;
}

.weight-container {
  position: absolute;
  right: 10px;
  bottom: 10px;
  width: 81px;
  height: 81px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  border-radius: 50%;
  background-color: #1698d9;
}
.weight-text {
  color: #ffffff;
  font-family: TrebuchetMS, sans-serif;
  font-size: 21px;
  font-weight: 400;
  line-height: 22px;
}

.weight-text--value {
  font-size: 42px;
  margin-bottom: 5px;
}

.card-description {
  text-align: center;
  color: #fff;
  max-width: 320px;
  font-size: 12px;
}
.card-description--disabled {
  color: #adad43;
}
.link {
  color: #1698d9;
  border-bottom: 1px dashed #1698d9;
  text-decoration: none;
}

@media (max-width: 1120px) {
  .card-wrapper {
    margin-right: 0;
  }
  .card-wrapper:last-child {
    padding: 0 20px;
  }
  @media (max-width: 767px) {
    .card-container {
      flex-direction: column;
      align-items: center;
    }
    .card-wrapper {
      margin-left: 0;
    }
    .card-wrapper:last-child {
      padding: 0;
    }
  }
}

.main-container {
  position: relative;
  background-image: url(/cat-food/static/media/backgground.0eb38956.jpg);
  min-height: 100vh;
}
.main-wrapper {
  position: relative;
  z-index: 5;
  padding: 50px 20px 60px;
  margin: 0 auto;
}
.main-shadow {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-image: linear-gradient(
    to top,
    rgba(0, 0, 0, 0.5) 0%,
    rgba(0, 0, 0, 0) 50%,
    rgba(0, 0, 0, 0.5) 100%
  );
}

.main-title {
  text-shadow: 0 1px 0 #000000;
  color: #ffffff;
  font-family: "Exo 2.0 Thin", arial;
  font-size: 36px;
  font-weight: 400;
  margin: 0 0 23px 0;
  text-align: center;
}

